import React from 'react';
import { Handle } from 'react-flow-renderer';
import outputlogo from '../outputlogo.png'
import ReactMarkdown from 'react-markdown';
const OutputNode = ({ data }) => {
  return (
    <div className="custom-node-output">
      <div className='input-header'> 
      <img src={outputlogo} alt='outpulogo'/>

      <strong style={{fontSize:'0.9rem'}}>OUTPUT</strong>
      </div>
      
      <label>Output</label>
      
        <div style={{ border: '1px solid #ccc', padding: '5px', borderRadius: '4px',  height:'100px' , overflow:'scroll', overflowX:'hidden', scrollbarWidth:'3px'}}>
        <ReactMarkdown>{data.outputText || ''}</ReactMarkdown>
      </div>
      <Handle type="target" position="left" />
    </div>
  );
};

export default OutputNode;
